.main { width: 350px; }

.container {
    max-width: 1000px;
    margin: 0 auto;
}
.header {
    text-align: center;
    margin-bottom: 40px;
}
.inner-container {
    display: flex;
    justify-content: space-around;
}

.btn { position: absolute; background: green; border: 1px solid black; border-top-color: #00b300; border-left-color: #00b300; padding: 0; margin: 0; display: inline-block; font-size: 15px; width: 100%; height: 100%; cursor: pointer; color: #fff; font-family: 'Roboto', sans-serif; border-radius: 50%; outline: none; }

.btn-label { padding: 12px; white-space: nowrap; }

.btn.btn-reset { background: darkgreen; border: 1px solid black; border-top-color: #009700; border-left-color: #009700; }

.btn.disabled { cursor: not-allowed; }

.visuallyhidden { position: absolute; overflow: hidden; clip: rect(0 0 0 0); height: 1px; width: 1px; margin: -1px; padding: 0; border: 0; }
